/* ==============================================================
   Fullscreen
   style declarations for viewObjectFullscreen.xhtml 
   ==============================================================*/
/* FULLSCREEN */
#fullscreen {
	padding: 0;
    background-color: @white; 
}
.fullscreen {
    position: relative;
    height: 100%;
    overflow: hidden;
    /* HEADER */
    &__header {
    	position: fixed;
    	top: 0;
    	right: 0;
    	left: 0;
    	display: flex;
    	align-items: center;
    	height: 70px;
    	border-bottom: 1px solid @fsMiddlegray;
    	background-color: @white;
    	transition: all .3s linear;
    	opacity: 1;
    	z-index: 1050;
    	&.fullscreen {
    		top: -70px;
    	}
    }
    &__header-brand,
    &__header-controls,
    &__header-exit {
    	padding-right: 15px;
    	padding-left: 15px;
    	a, 
    	button, 
    	span,
    	select {
    		color: @fsDarkgray;
			fill: @fsDarkgray;
    		&:hover,
    		&:active,
    		&:focus {
    			color: @fsDarkgray;
    			text-decoration: none;
    		}
    	}
    	.custom-control--select::after {
    		border-top-color: @fsDarkgray;
    	}
    }
    &__header-brand {
    	padding-top: 15px;
    	padding-bottom: 15px;
    	img {
    		max-height: 40px;
    	}
    	span {
    		.mx-hidden;
    	}
    }
    &__header-controls {
    	display: flex;
    	align-items: center;
    	&__options {
    		.btn--clean {    			
	    		width: 32px;
	    		height: 32px;
	    		margin-left: 10px;
		    	&[data-show="thumbs"] {
		    		background-image: url("@{images}/icons/icon_fs-thumbs.svg");
		    		background-repeat: no-repeat;
		    		background-position: center center;
		    		background-size: 22px;
		    		&.in {
			    		background-image: url("@{images}/icons/icon_fs-close.svg");    			
		    		}
		    	}
		    	.fa {
		    		font-size: 20px;
		    	}
    		}
    	}
    }
    &__header-docstruct-navigation {
    	margin-left: auto;
    	span {
    		opacity: 0.4;
    		cursor: not-allowed;
    	}
    }
    &__header-docstruct-navigation-back,
    &__header-docstruct-navigation-next {
    	padding: 0.4rem 0.7rem;
    	border-radius: 4px;
    	background: @labelGray;
    	color: @white;
    	font-size: 1.4rem;
    	&:hover,
    	&:focus, 
    	&:active {
    		text-decoration: none;
    		color: @white;
    		background: @labelHoverGray;
    	}
    }
	&__header-docstruct-navigation-back {
    	margin-right: 1rem;
	}
    &__header-docstruct-navigation-next {}
    &__header-exit {
    	margin-left: auto;
    	a {
    		display: inline-block;
    		width: 32px;
    		height: 32px;
    		background-image: url("@{images}/icons/icon_fs-exit.svg");
    		background-repeat: no-repeat;
    		background-position: center center;
    		background-size: 28px;
    	}
    }
    /* VIEW */
	&__view {
		position: relative;
		transition: all .3s linear;
	}
	/* PANEL CONTROLS */
	&__view-sidebar-panel-controls {
		position: absolute;
		top: 85px;
		right: 15px;
		z-index: 1040;
		[data-close="all-tabs"],
		[data-open="all-tabs"] {
			width: 28px;
			height: 28px;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 14px;
		}
		[data-close="all-tabs"] {
			background-image: url("@{images}/icons/icon_fs-minus.svg");
		}
		[data-open="all-tabs"] {
			background-image: url("@{images}/icons/icon_fs-plus.svg");
		}
	}
	/* IMAGE */
    &__view-image,
	&__view-image-inner,
	.view-object{
		&__object-3d-world,
		&__image-map {
			height: 100vh;
			img {
			    display: block;
				height: 100%;
			    margin: 0 auto;
			}
		}
	}
	&__view-image {}
	&__view-image-inner {
		position: relative;
		background: repeating-linear-gradient(
			-55deg,
			#222,
			#222 10px,
			#333 10px,
			#333 20px
		);
		.view-object{
			&__object-3d,
			&__image {
				padding: 0;
				border: 0;
				background-color: transparent;
			}
		}
	}
	/* THUMBS */
	&__view-image-thumbs-wrapper {
		display: none;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		padding: 15px;
		padding-top: 85px;
		overflow-y: auto;
		z-index: 10;
		&.-rtl {
			direction: rtl;
		}
		&.-ltr {
			direction: ltr;
		}
	}
	&__view-image-thumbs {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-evenly;
	}
	&__view-image-thumb {
		position: relative;
		width: 175px;
		min-height: 250px;
		margin: 10px;
	}
	&__view-image-thumb-preloader {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: url("@{images}/infinity_loader.svg");
		background-repeat: no-repeat;
		background-position: center;
		background-size: 64px;
		z-index: 10;
	}
	&__view-image-thumb-image {
		img {
			display: block;
			margin: 0 auto;
			max-width: 100%;
			height: auto;
			max-height: 250px;
			opacity: 0;
			transition: all 0.3s ease-in;
			&.in {
				opacity: 1;
			}			
		}
		figcaption {}
	}
	&__view-image-thumb-image-order {
		padding: 5px;
		text-align: center;
		color: @white;
		opacity: 0;
		transition: all 0.3s ease-in;
		&.in {
			opacity: 1;
		}		
	}
	/* SIDEBAR */
	&__view-sidebar-open {
		position: absolute;
		top: 75px;
		right: -130px;
		padding: 10px;
		padding-top: 5px;
		background-color: @white;
		opacity: 0;
		transition: all .3s linear;
		z-index: 1010;
		&.in {
			right: 0;
			opacity: 1;
		}
		.btn {
			padding-left: 20px;
			color: @fsDarkgray;
			background-repeat: no-repeat;
			background-position: left 2px;
			background-size: 9px;
			background-image: url("@{images}/icons/icon_fs-left.svg");			
	    	overflow: hidden;
			&:hover,
			&:active,
			&:focus,
			&:active:focus,
			&.active,
			&.active.focus, 
			&.active:focus, 
			&.focus, 
			&:active.focus {
	    		color: @fsDarkgray;
	    		text-decoration: none;
	    	}
		}
	}
	&__view-sidebar {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		width: 500px;
		padding-top: 70px;
		background-color: rgba(255,255,255,0.9);
		overflow-y: auto;
		z-index: 100;
		.ui-resizable-handle {
			left: 0;
			border-right: 1px solid @fsMiddlegray;
			z-index: 998 !important;
			&:before,
			&:after {
				content: "";
				position: absolute;
				display: block;
				top: 50%;
				width: 1px;
				height: 40px;
				margin-top: -20px;
				background-color: @fsMiddlegray;
			}
			&:before {
				right: 2px;
			}
			&:after {
				right: 5px;
			}
		}
		.ui-resizable-w {
			width: 10px;
			cursor: w-resize;
		}
	}
	&__view-sidebar-inner {
		display: none;
		padding-left: 10px;
	}
	&__view-sidebar-controls {
		display: flex;
		align-items: center;
		padding: 15px;
		background-color: @white;
		.btn {
			width: 28px;
			height: 28px;
			background-repeat: no-repeat;
			background-position: center center;
			background-size: 14px;
		}
		[data-close="fs-sidebar"] {
			background-image: url("@{images}/icons/icon_fs-right.svg");
			background-size: 10px;
		}
	}
	/* SIDEBAR WORK TITLE */
	&__view-sidebar-work-title {
		padding: 15px;
		background-color: @white;
		word-break: break-word;
		h1, h2 {
			margin: 0;
    		line-height: 2rem;
			font-size: @fontNormal;
		}
	}
	/* SIDEBAR ACCORDEON */
	&__view-sidebar-accordeon {
		border-bottom: 1px solid @fsMiddlegray;
	}
	&__view-sidebar-accordeon-panel {
		position: relative;
		background-color: @white;
		&.fs-metadata {}
		&.fs-toc {
			.fullscreen__view-sidebar-accordeon-panel-body {
				padding-top: 0;
			}
		}
		&.fs-downloads {}
		&.fs-fulltext {}
		&.fs-search-hit-navigation {
			.mx-hidden;
		}
		&:first-child {
			h2, h3 {
				border-top: 0;
			}
		}
		&.fs-imagefilters {
			imageFilters {					
				display: none;
			}
		}
		&.share-image-area {
			ul {
				list-style: circle;
				li {
					margin-top: 10px;
					a {
						&:hover {
							cursor: pointer;
						}
					}
				}
			}
			.btn--full {
				margin-top: 10px;
				width: 100%;
			}
		}
	}
	&__view-sidebar-accordeon-panel-title {
		position: relative;
		margin: 0;
		padding: 10px 15px;
		font-size: @fontNormal;
		border-top: 1px solid @fsMiddlegray;
		background-color: @white;
		cursor: pointer;
		z-index: 20;
		&:hover {
			background-color: @fsLightgray;
		}
		span {}
		&.in {
			background-color: @fsLightgray;
		}
	}
	&__view-sidebar-accordeon-panel-body {
		display: none;
		padding: 15px;
		overflow-y: auto;
		& > h1 {
			.mx-hidden;
		}
	}
	&__view-sidebar-search-in-current-item {
		.input-group {
			.form-control {
				border-left: 0;
			}
		}
	}
	/* MODIFICATION */
	.widget {
		margin: 0;
	}
}

@{RTL} {
	.fullscreen {
		&__view-image-thumbs-wrapper {
			right: unset;
			&.-ltr {
				direction: rtl;
			}
		}
		&__view-sidebar-work-title {
			word-break: normal;
		}
		&__view-sidebar-panel-controls {
			right: unset;
			left: 15px;
		}
	    &__header-exit {
	    	margin-left: unset;
	    	margin-right: auto;	
	    }
	}
}
/* BREAKPOINT FOR LARGE DESKTOPS */
@media screen and (max-width: @largeBreakpoint) {}
 
/* BREAKPOINT FOR DESKTOPS */
@media screen and (max-width: @mediumBreakpoint) {}

/* BREAKPOINT FOR TABLETS */
@media screen and (max-width: @smallBreakpoint) {
	.fullscreen {
		&__view-sidebar {
			width: 50%;
			padding-left: 0;
			border-left: 1px solid @fsMiddlegray;
		}
		&__view-sidebar-accordeon-panel {
			&.fs-imagefilters,
			&.share-image-area {
				display: none;
			}
		}
	}
}
/* BREAKPOINT FOR SMARTPHONES */
@media screen and (max-width: @xSmallBreakpoint) {
	.fullscreen {
		&__header-brand {
			span {
				display: none;
			}
		}
		&__view-sidebar {
			width: 100%;
		}
		&__view-sidebar-open {
			width: 32px;
			height: 32px;
			padding: 0;
			.btn {
				display: block;
				width: 100%;
				height: 100%;
				padding: 0;
				background-position: center center;
				span {
					.mx-hidden;
				}
			}
		}
	}
}