/* ==============================================================
	Global style declarations for Admin Backend
   ==============================================================*/
/* ADMIN LAYOUT */

.admin {
	/* BACKEND FONT + FALLBACK FONTS */
	font-family: @fontBackend;
	display: flex;

	/* BASIC ELEMENTS */
	h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
		font-weight: 600;
	}
	
	dt {
		font-weight: 600;
	}

	/* LAYOUT GENERAL */
	&__content {
		min-height: 100vh;
		width: calc(~"100vw - 250px");
		background-color: @neutralgray2;
		padding: 1.5rem;
		position: relative;
		padding-top: 5rem;
		&.fixed-top,
		&.fixed-bottom {
			position: fixed;
            padding-right: 15px;
		}
		&.fixed-top {
			top: 0;
		}
		&.fixed-bottom {
			bottom: 0;
		}
	}
    &__content-inner {
        font-size: 1.4rem;
    }
	&__content-inner-title {
		margin: 0;
		padding: 15px 0 0 15px;
		font-size: 3.2rem;
	}
	&__content-inner-title-exit {
		display: block;
    	width: 32px;
    	height: 32px;
    	background-image: url("@{images}/icons/icon_fs-exit.svg");
    	background-repeat: no-repeat;
    	background-position: center center;
    	background-size: 24px;
    	margin-left: auto;
	}
	&__content-inner-content {
		padding: 15px;
		h3.admin__h3 {
			margin-bottom: 1rem;
			font-size: 1.8rem;
		}
	}
	&__title-bar {
		display: flex;
		align-items: center;
		padding: 15px;
	}
	&__title {
		margin: 0;
		padding: 0;
		font-size: 3.2rem;
		line-height: 1;
	}	
	&__title-bar-action {
		margin-left: 3rem;
		font-size: 1.4rem;
		padding: 0.4rem 0.8rem;
	}
	&__content-background {
		height: 100%;
		width: 100%;
		background: @neutralgray2;
		position: fixed;
	}

	/* LINK MODIFIERS */
	.rss-elem-title a {
		color: @blue;
		&:hover {
			color: @hoverblue;
		}
	}
	&__action-links {
		margin-right: 1.4rem;
	}
	.-light {
		color: #a9a9a9;
		cursor: default;
		padding: 0;
	}
	.-redlink {
		color: @red;
		&:hover {
			color: @red;
			text-decoration: underline;
		}
		&:active {
			color: @red;
		}
		&:focus {}
	}
	.-textlink {
		color: initial;
		text-decoration: none;
		cursor: pointer;
		&:hover {
			color: initial;
			text-decoration: none;
			cursor: pointer;
		}
		&:active {
			color: initial;
			text-decoration: none;
			cursor: pointer;
		}
		&:focus {}
	}
	/* BOOTSTRAP BADGES OVERRIDE FOR BACKEND */
    .badge {
        border-radius: 0;
        font-weight: normal;
        margin-right: .5rem;
        margin-bottom: .5rem;
        font-size: 1.4rem;
        background-color: transparent;
        padding: 0.3rem 0.6rem;
        &-light {
			color: #a9a9a9;
			cursor: default;
			padding: 0;
			margin-left: 0;
		}
        &-violet {
            color: #8947CC;
            border: 1px solid #8947CC;
        }
        &-primary {
            color: #3365a9;
            border: 1px solid #3365a9;
        }
        &-info {
            color: #008295;
            border: 1px solid #008295;
        }
        &-dark {
            color: #343a40;
            border: 1px solid #343a40;
        }
        &-danger {
            color: @white;
            background-color: #dc3545;
        }
        &-success {
            color: #008a00;
            border: 1px solid #008a00;
        }
        &-secondary {
            color: #74422B;
            border: 1px solid #74422B;
        }
		&-warning {
			background-color:#ffc107;
			color:#212529;
		}
		&-gray {
			background-color: #ccc;
			color:#212529;
			text-transform: uppercase;
            border: 1px solid #ccc;
		}		
        &-hover {
            .badge-success {
	            &:hover {
		    	    color: @black;
		        }
            }
		    .badge-dark {
		    	&:hover {
                    background-color: #F5F5F5;
		    		color: @black;
		    	}
		    }
        }
    }
    &__info-text {
        margin-bottom: 3rem;
        .badge {
        	background-color: @white;
            margin-right: 0;
        }
		.badge-warning {
			background-color:#ffc107;
			color:#212529;
		}

	/* BLOCK AND BOX STYLING */
    }
	&__default-block {
		margin-bottom: 3rem;
		padding: 2rem;
		background-color: #fff;
		border: 1px solid @middlegray;
		.form-row:last-of-type {
			margin-bottom: 0;
		}
		&.-danger {
			border-top: 5px solid @redBar;
		}
		&.-warning {
			border-top: 5px solid @yellowBar;
		}
		&.-success-box {
		border: 1px solid #ABD9B5;
    	border-top: 5px solid #ABD9B5;
		}
		&.-info-box {
		border: 1px solid @blue;
    	border-top: 5px solid @blue;
		}
		&.-gray-box {
		border: 1px solid @gray;
    	border-top: 5px solid @gray;			
		}
		&-description {
	    	margin-bottom: 3rem;
            &-empty {
                font-style: italic;
                color: @middlegray;
            }
	    }	
	}
	&__final-actions {
		margin-top: -1rem;
	}
	&__entry-list {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		&.-w33 {
			&::after {
			    content: '';
			    flex: 0 0 calc(33.3333% - 2rem);
			    width: calc(33.3333% - 2rem);
			    max-width: calc(33.3333% - 2rem);
			}
		}
		&.-w25 {
			&::after {
			    content: '';
			    flex: 0 0 calc(25% - 2rem);
			    width: calc(25% - 2rem);
			    max-width: calc(25% - 2rem);
			}
		}
	}
	&__boxed-entry {
		background: @white;
		padding: 2rem;
		padding-bottom: 4rem;
		margin-bottom: 2.25rem;
		transition: background 0.2s ease;
		border: 1px solid @middlegray;
		position: relative;
		&:hover {
			//background: @lightgray2;
			box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
			.admin__boxed-entry-actions {
				opacity: 1.0;
			}
		}
		&.-no-actions {
			padding-bottom: 2rem;
		}
		&.-w25 {
			flex: 0 0 calc(25% - 1.5rem);
			width: calc(25% - 1.5rem);
			max-width: calc(25% - 1.5rem);
		}
		&.-w33 {
			flex: 0 0 calc(33.3333% - 1.5rem);
			width: calc(33.3333% - 1.5rem);
			max-width: calc(33.3333% - 1.5rem);
		}
		&.-w50 {
			flex: 0 0 calc(50% - 1rem);
			width: calc(50% - 1rem);
			max-width: calc(50% - 1rem);
		}
		&.-w66 {
			flex: 0 0 calc(66.6666% - 0.75rem);
			width: calc(66.6666% - 0.75rem);
			max-width: calc(66.6666% - 0.75rem);
		}
		&.-w100 {
			flex: 0 0 calc(100%);
			width: calc(100%);
			max-width: calc(100%);
		}
		&.-std-bottom-pad {
			padding-bottom: 2rem;
		}
		&.-danger {
			border-top: 5px solid @redBar;
		}
		&.-warning {
			border-top: 5px solid @yellowBar;
		}
		&-actions {
			opacity: 0.0;
			position: absolute;
			bottom: 1.5rem;
			left: 2rem;
			transition: opacity 0.2s ease;
			a {
				font-size: 1.4rem;
				margin: 0 0.9rem;
				&:first-of-type {
					margin-left: 0;
				}
			}
		}
	    &-description {
	    	margin-bottom: 3rem;
            &-empty {
                font-style: italic;
                color: @middlegray;
            }
	    }
	}
	&__boxed-entry-progress {
		background: @grayBar;
		height: 6px;
		width: 100%;
		position: absolute;
		bottom: 0;
		left: 0;
		display: flex;
	}
	&__boxed-entry-progress-complete {
		background: @greenBar;
	}
	&__boxed-entry-progress-partially {
		background: @yellowBar;
	}
	&__sidebar-progressbar {
		background: @grayBar;
		height: 30px;
		width: 100%;
		display: flex;
	}
	&__license-unconfigured-entry {
		&:hover {
			color: #212529;
		}
	}
	&__dashboard-translations-progress {
		background: @grayBar;
		height: 6px;
		width: auto;
		position: relative;
		bottom: 0;
		left: 0;
		display: flex;
		margin-left: 3.3rem;
	}
	&__dashboard-translations-progress-complete {
		background: @greenBar;
	}
	&__dashboard-translations-progress-partially {
		background: @yellowBar;
	}
	&__dashboard-translations-progress-error {
		background: @redBar;
		flex: 0 0 100%;
		max-width: 100%;
	}
	&__translations-not-translated-entry {
		margin-top: 2rem;
		margin-bottom: 0.5rem;
		&:hover {
			color: #212529;
		}
	}
	&__translations-global-key-wrapper {
		font-size: 12px;
		color: #7c7c7c;
	}
	&__translations-fields {
		
	}
	&__translations-global-key {
	}
	&__translations-global-hint {
		display: none;
	}
	&__translations-filter-wrapper {
		display: flex;
		margin-bottom: 1rem;
		margin-top: 1rem;
		position: relative;
	}
	&__translations-search-action {
		line-height: 1;
		color: #555;
		text-align: center;
		background-color: #eee;
		border: 1px solid #ccc;
		display: flex;
		padding: 0;
		align-items: center;
		justify-content: center;
		width: 3.5rem;
		max-width: 3.5rem;
		flex: 0 0 3.5rem;
		display: flex;
		justify-content: center;
		color: #212529;
	}
	&__translations-search-input {
		padding-right: 2.5rem;
	}
	&__translations-search-clear {
		display: none;
		cursor: pointer;
		padding: 1rem;
		position: absolute;
		right: 36px;
		top: 0;
		font-size: 1.6rem;
		line-height: 1.1rem;
	}
	&__translations-search-button {
		color: #555;
		width: 4.5rem;
		height: 100%;
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__sidebar-progressbar-complete {
		background: @greenBar;
	}
	&__sidebar-progressbar-partially {
		background: @yellowBar;
	}
	&__sidebar-progress-header {
		display: flex;
	}
	&__sidebar-progress-count {
		text-align: right;
		margin-left: auto;
		font-size: 14px;
		align-self: center;
	}
	&__translations-all-keys-list div:last-child {
	    border-bottom: 1px solid #ccc;
	}
	&__translations-not-translated,
	&__translations-partially-translated,
	&__translations-fully-translated {
		position: relative;
		word-wrap: break-word;
		padding: 0.5rem 1rem;
		border-top: 1px solid #ccc;
		border-left: 1px solid #ccc;
		border-right: 1px solid #ccc;
		a {
			display: block;
			color: #444;
			&:hover,
			&:active,
			&:focus {
				text-decoration: none;
			}
		}
	}
	&__translations-not-translated {
		background-color: #EEEEEE;
		&:hover {
			background: darken(#EEEEEE, 3%);
		}
	}
	&__translations-partially-translated {
		background-color: #F7E3B5;
		&:hover {
			background: darken(#F7E3B5, 3%);
		}
	}
	&__translations-fully-translated {
		background-color: #D4EDDB;
		&:hover {
			background: darken(#D4EDDB, 3%);
		}
	}	
	&__translations-selected-key {
		background-color: @white;
		&:hover {
			background: @white;
		}
		a {
			color: @blue;
			font-weight: 600;
		}
		&:before {
			position: absolute;
			height: 100%;
			width: 4px;
			left: -4px;
			top: 0;
			content: '';
			display: block;
			background: @blue;	
			}
	}
	&__grab-indicator {
		padding-top: 6rem;
	}
	
	&__label-required-field {
		padding-left: 0.5rem;
		font-size: 1.2rem;
	}
	&__translations-overview-number {
		margin-top: 0.8rem;
		font-size: 1.2rem;
	}
	
	/* ADMIN BACKEND FLEX TABLE */
	&__table-tools {
		width: 100%;
		display: flex;
		justify-content: space-between;
		margin-bottom: 0.5rem;
	}
	&__table-paginator {
		li {
			margin-right: 0.5rem;
			border: none;
		}
		a {
			background: transparent;
			font-size: 1.4rem;
		}
		span {
			font-size: 1.4rem;
		}
	}
	&__table-filter {
		.input-group > .form-control {
			width: 250px;
		}
		.input-group-append {
			font-size: 1.4rem;
			font-weight: 400;
			line-height: 1;
			color: #555;
			text-align: center;
			background-color: #eee;
			border: 1px solid @middlegray;
			border-radius: 4px;
			display: flex;
			padding: 0 1rem;
			align-items: center;
			justify-content: center;
		}
	}
	&__table-content {
		background: @white;
		padding: 0 2rem 0rem 2rem;
		border: 1px solid @middlegray;
        margin-bottom: 2rem;
        &.-slim {
        	border: none;
        	padding: 0;
        }
	}
	&__table-title {
		margin-bottom: 0rem;
		font-weight: 600;
		border-bottom: 1px solid @gray;
		padding: 1rem 0;
        font-size: 1.6rem;
        position: sticky;
		top: 3.5rem;
		background: white;
		z-index: 5;
        margin-left:-2rem;
        padding-left:2rem;
        margin-right:-2rem;
        padding-right:2rem;
        z-index: 950;

	}
	// SORTING ARROW
	&__table-sorting {
		flex: 1;
		margin-right: 2rem;
		&:hover {
			text-decoration: none;
			.admin__table-sorting-arrow-hidden {
				opacity: 1;
			}
			.admin__table-sorting-arrow-up, .admin__table-sorting-arrow-down {
				transform: rotate(180deg);
				transform-origin: 50% 58%;
			}
		}
	}
	&__table-sorting-arrow-hidden {
		opacity: 0;
		transition: all 0.2s ease;
	}
	&__table-sorting-arrow-up {}
	&__table-sorting-arrow-down {}
	&__table-sorting-arrow-hidden,
	&__table-sorting-arrow-up, 
	&__table-sorting-arrow-down {
		color: @black;
		margin-left: 0.6rem;
	}
	&__table-sorting-heading {}
	
	&__table-entry {
		/* opacity: 0;
		animation: fadein 0.2s ease-in 0s 1 forwards;
		.delay-animation(@numitems, @delay); */
		transition: background 0.2s ease;
		padding: 1.5rem 2rem;
		margin-left: -2rem;
		margin-right: -2rem;
		&:hover {
		    background: @lightgray2;
		    .admin__table-action-link {
		    	transition: opacity 0.2s ease;
		    	opacity: 1.0;
		    }
		}
		&.-unpublished {
			.admin__table-name, .admin__table-additional-info {
				opacity: 0.35;
			}
		}
	}
	&__table-img {
        margin-right: 2rem;
		flex: 0 0 80px;
		img {
			max-width: 100%;
			max-height: 80px;
		}
	}
	&__table-data {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: start;
	}
	&__table-name {
		font-size: 1.8rem;
		.badge {
			margin-left:10px;
			background-color:#ffc107;
			color:#212529;
			font-size:1.2rem;
			vertical-align:middle;
		}
	}
	&__table-action-link {
		opacity: 0;
		font-size: 1.4rem;
		transition: opacity 0.2s ease;
		margin-right: 1.4rem;
	}
	&__table-email {
		font-size: 1.4rem;
	}
	&__table-badges {
		overflow: hidden;
	}
	&__table-last-login {
		font-size: 1.4rem;
		}
	&__search-input-wrapper {
		position: relative;
	}
	&__search-input {
		padding-right: 2.5rem;
		width: 250px;
	}
	&__search-clear {
		display: none;
		cursor: pointer;
		padding: 1rem;
		position: absolute;
		right: 5px;
		top: 0;
		font-size: 1.6rem;
		line-height: 1.1rem;
	}
	&__search-action {
		font-size: 1.4rem;
		min-width: 120px;
		text-align: center;
	}

	/* SIDEBAR */
	&__sidebar-background,
	&__sidebar {
		width: 250px;		
		background-color: @blue; 
	}
	&__sidebar-background {  
		position: fixed;
		top: 0;
		bottom: -120px;
		z-index: 1;
	}
	&__sidebar {
		position: relative;
		padding: 1.5rem;
		font-size: @fontNormal;
		z-index: 1010;
		padding-top: 0;
		&.fixed-top,
		&.fixed-bottom {
			position: fixed;
		}
		&.fixed-top {
			top: 0;
		}
		&.fixed-bottom {
			bottom: 0;
		}
		a, button {
			color: @white;
			&:hover,
			&:active,
			&:focus {
				text-decoration: none;
				color: @white;
			}
		}
	}
	&__sidebar-divider-top {
		border-top: 1px dashed #eee;
		padding-top: 1.4rem;
	}
	&__sidebar-inner {
		padding-top: 3rem;
		h1 {
			line-height: normal;
			margin: 0;
			margin-bottom: 0.7rem;
			font-size: 2.5rem;
			color: @white;
			font-weight: 600;
			padding: 1.7rem 0 0.2rem 0;
			a {
				display: flex;
				align-items: center;
				max-width: 100%;
			}
			img {
				max-width: 30%;
				margin-right: 10px;
			}
		}
	}
	&__sidebar-logout {
		margin-right: -15px;
		margin-left: -15px;
		padding-top: 10px;
		padding-right: 15px;
		padding-left: 15px;
		border-top: 1px solid @white;
	}

	

	/* ADMIN FORMS */
	&__form-label {}
	&__form-input {
		flex-grow: 1;
		.ui-autocomplete {
			border: 1px solid #ced4da;
			background: white;
			overflow-x: hidden;
			overflow-y: scroll;
			height: 400px;
		}
	}
	&__form-help-icon {
		display: flex;
		justify-content: center;
		align-items: flex-start;
	}
	&__form-checkbox {}
	&__form-help-text {
		display: none;
		margin-top: 0.4rem;
		font-size: @fontSmall;
	    font-style: italic;
	    color: @inlineStatic;
		&.in {
			display: inline-block;
		}
	}
	&__dashboard-user-welcome {
		display: flex;
	}
	&__dashboard-user-welcome-left {
		flex: 0 0 calc(~"100% - 135px");
		max-width: calc(~"100% - 135px");
	}
	&__dashboard-user-welcome-right {
		flex: 0 0 120px;
		max-width: 120px;
		margin-left: 1.5rem;
	}
	&__dashboard-user-icon-wrapper {
		flex: 0 0 150px;
		min-width: 150px;
	}
	&__dashboard-user-icon {
		position: relative;
		max-width: 100%;
		object-fit: cover;
		width: 120px;
		height: 120px;
	}
	// FANCY RADIO SWITCH/TOGGLE
	&__radio-switch {
	    position: relative;
		width: 18rem;
		height: 2.5rem;
		font-size: 0;
	    input {
	        position: absolute;
	        top: 0;
	        z-index: 2;
	        opacity: 0;
	        cursor: pointer;
			height: 2.5rem;
			width: 6rem;
			left: 3.5rem;
			margin: 0;
			& ~ input:checked ~ .toggle-outside {
				background: #3365a9;
			}
			& ~ input:checked ~ .toggle-outside .toggle-inside {
				left: 3.5rem;
				background: #fff;
			}
	    }
	    input:checked {
			z-index: 1;
			& ~ .toggle-outside .toggle-inside {
				left: 0.5rem;
				background: #fff;
				border: 1px solid rgba(0,0,0,0.2);
			}	
		}
		input:checked + label {
			opacity: 1;
			cursor: default;
		}
		input:not(:checked) + label:hover {
			opacity: 0.5;
		}
		label {
			color: #000;
			opacity: 0.55;
			transition: opacity 0.25s ease;
			cursor: pointer;
			font-size: 1.5rem;
			line-height: 2.5rem;
		  	display: inline-block;
