/* ==============================================================
   Widget Chronology
   style declarations for widget_chronology.xhtml
   ==============================================================*/
/* WIDGET CHRONOLOGY */
/* BLOCK */ 
.widget-chronology-slider {
	display: flex;
	flex-wrap: wrap;
    font-size: @fontLarge;
	color: @black;
	&__topbar {
		flex: 0 0 100%;
		display: flex;
	}
	/* ELEMENT */
	&__item {
		&.chronology-slider-label,
		&.chronology-slider-actions {
			display: flex;
		}
		&.chronology-slider-label {
			margin-right: auto;
		}
		&.chronology-slider-actions {}

		&.chronology-slider-actions {
			ul {
				.mx-list-reset;
				display: flex;
				justify-content: flex-end;
			}
			li {
				text-align: right;
			}
			img {
				width: 24px;
				height: 24px;
			}
			[data-reset="chrono-slider"] {} 
		}
		&.chronology-slider-action-loader,
		&.chronology-slider-action-reset {
			display: flex;
			flex-wrap: nowrap;
			z-index: 200;
			justify-content: center;
			align-items: center;
			padding: 0 9px;
			visibility: hidden;
			&.active {
				visibility: visible;
			}
		}
		&.chronology-slider-action-loader {}
		&.chronology-slider-action-reset {}
		&.chronology-slider-start,
		&.chronology-slider-end {
			flex: 0 0 50%;
			font-size: @fontMedium; 
		}
		&.chronology-slider-start {}
		&.chronology-slider-end {
			text-align: right;
		}
		&.chronology-slider {
			padding: 0.6rem 0 1rem 0;
			flex: 0 0 100%;
		}
	}
	&__item-input {
	/* &::selection {
	    	background: rgba(201, 102, 102, 0.6);
	    	color: white;
		} */
			background: transparent;
			width: 4em;
			border: 1px solid transparent;
			border-radius: 0;
			text-align: inherit;
			padding: 0.3rem;
		&:focus {
			background-color: rgba(0, 0, 0, 0.06);
		}
	}
	&__action-reset-button {
		font-size: 1.7rem;
		border: none;
		background: none;
	}
}

#chronoSlider {

	height: 1rem;
	border-radius: 5px;
	border-color: @lightgray;
	background-image: none;
	background-color: @lightgray;
	.ui-slider-range,
	.ui-slider-handle {
	    border-radius: 4px;
		background-image: none;
        &:focus {
        	outline: none;
        }
	}
	.ui-slider-range {
	    border-color: @blue;
		background-color: @blue;		
	}
	.ui-slider-handle {
		width: 1rem;
	    height: 1rem;
	    top: 0;
	    margin: 0;
	    border-color: @gray;
	    background-color: @middlegray;
	    cursor: pointer;
	}
}

@{RTL} .widget-chronology-slider {
	&__item {
		&.chronology-slider-label,
		&.chronology-slider-actions {
		}
		&.chronology-slider-label {
			margin-right: unset;
			margin-left: auto;
		}
	}
}

/* BREAKPOINT FOR LARGE DESKTOPS */
@media screen and (max-width: @largeBreakpoint) {}

/* BREAKPOINT FOR DESKTOPS */
@media screen and (max-width: @mediumBreakpoint) {}

/* BREAKPOINT FOR TABLETS */
